  --grey-6: #ececec;
  --grey-7: #d9d9d9;
  --grey-8: #efefef;
  --grey-9: #dedede;
  --grey-10: #dfdfdf;
  --grey-opacity: rgba(0, 0, 0, 0.03);
  --grey-opacity-2: rgba(0, 0, 0, 0.42);
  --black: #000;
  --green: #43c59e;
  --green-opacity: rgba(67, 197, 158, 0.19);
  --green-opacity-2: rgba(67, 197, 158, 0.11);
  --pink: #eb236b;
  --red: #ef476f;
  --red-2: #f40000;
  --red-opacity: rgba(239, 71, 111, 0.19);
  --red-opacity-2: rgba(239, 71, 111, 0.11);
  --violet: #3d3bff;
  --gold: #ffe381;
  --yellow: #ffaa05;
  --white-opacity: rgba(255, 255, 255, 0.5);
}

