input[type=checkbox],
input[type=radio] {
  height: 16px;
  width: 16px;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  display: inline-block;
  vertical-align: top;
  position: relative;
  cursor: pointer;
  border: 1px solid var(--bc, #dcdcdc);
  background: var(--b, #fff);
  transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
}
input[type=checkbox]:after,
input[type=radio]:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  position: absolute;
  transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
  opacity: var(--o, 0);
}
input[type=checkbox]:checked,
input[type=radio]:checked {
  --b: #0075ff;
  --bc: #0075ff;
  --d-o: 0.3s;
  --d-t: 0.6s;
  --d-t-e: cubic-bezier(0.2, 0.85, 0.32, 1.2);
  --o: 1;
}
input[type=checkbox]:hover:not(:checked),
input[type=radio]:hover:not(:checked) {
  --bc: #0075ff;
}
input[type=checkbox] + label,
input[type=radio] + label {
  margin-left: 15px;
  font-weight: normal;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
}
@media (max-width: 500px) {
  input[type=checkbox] + label,
  input[type=radio] + label {
    margin-left: 10px;
  }
}

input[type=checkbox] {
  margin: 2px 0 0;
  border-radius: 4px;
}
input[type=checkbox]:after {
  width: 4px;
  height: 7px;
  border: 1px solid #fff;
  border-top: 0;
  border-left: 0;
  left: 5px;
  top: 3px;
  transform: rotate(var(--r, 20deg));
}
input[type=checkbox]:checked {
  --r: 43deg;
}

input[type=radio] {
  border-radius: 50%;
}
input[type=radio]:after {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #fff;
  opacity: 0;
  transform: scale(var(--s, 0.7));
  left: 2px;
  top: 2px;
}
input[type=radio]:checked {
  --s: 1;
}
input[type=radio]:checked::after {
  opacity: 1;
}

